#contact{
    margin-top: 0rem;
}

.container.contact_container{
    width: 58%;
    display: grid;
    grid-template-columns: 30% 58%;
    gap: 12%;
}

.contact_options{
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

.contact_option{
    background: var(--color-bg-variant);
    padding: 0rem;
    border-radius: 0.8rem;
    text-align: center;
    border: 1px solid transparent;
    transition: var(--transition);
    gap: 1.2rem;
    margin-top: 0rem;
    align-items: center;
}


.icon{
    font-size: 1.rem;
    align-items: center;
    margin-left: 1rem;
}

.contact_option a{
    margin-top: 0.7rem;
    display: inline-block;
    font-size: 0.8rem;
}


form {
    display: flex;
    flex-direction: column;
    gap: 1.2rem;
}

input,
textarea{
    width: 100%;
    padding: 1.5rem;
    border-radius: 0.5rem;
    background: transparent;
    border: 2px solid var(--color-primary-variant);
    resize: none;
    color: var(--color-white);
}




/*====================MEDIA QUERY (TABLETS OR MEDIUM DEVICE) ==================================*/

@media screen and (max-width: 1024px) {
    .container.contact_container{
        grid-template-columns: 1fr;
        gap: 2rem;
    }
}
  
    
    /*====================MEDIA QUERY (MOBILE OR SMALL DEVICE) ==================================*/
    
@media screen and (max-width: 600px) {
    
    .container.contact_container{
       width: var(--container-width-sm);
    }
}    